.grid {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  column-gap: 20px;

  @include onDesktop {
    grid-template-columns: repeat(12,1fr);
    column-gap: 24px;
  }

  &--desktop {
    display: block;

    @include onDesktop {
      display: grid;
    }
  }

  // &__item {
  //   &--1-4 {
  //     grid-column: 1 / 5;
  //   }
  // }

  // &__item {
  //   &--7-12 {
  //     grid-column: 7 / 13;
  //   }
  // }

  &__item {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }

        &--desktop-#{$start}-#{$end} {
          @include onDesktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
